<!--
 * @Description: 操作按钮
 * @Author: 徐强
 * @Date: 2021-11-17 09:34:52
-->
<template>
  <div style="padding:14px 0;display: flex;  justify-content: space-between;" :style=" title ? { 'border-bottom': '2px solid #e8e8e8','margin-bottom':'15px' } : '' ">
    <!-- 传入title表示详情页面的操作 -->
    <div v-if="title" style="display: flex;    align-items: center;">
      <div class="left-logo" :style=" color ? { 'background-color': color } : { 'background-color': '#1890ff' } " />
      <slot name="leftTitle">
        <span style="font-weight: 600;">{{ title }}</span>
      </slot>
    </div>
    <div style="display: flex;">
      <!-- <el-button type="button" class="btn-primary" @click="click()">
        <i class=" icon el-icon-refresh-right" aria-hidden="true" />
        搜索
      </el-button> -->
      <el-input v-model="input3" placeholder="请输入内容" class="input-with-select">
        <el-button slot="append" type="button" class="btn-primary" @click="reload()">
          <i class=" icon el-icon-refresh-right" aria-hidden="true" />
          搜索
        </el-button>
      </el-input>
      <el-button type="button" class="btn-primary" @click="reload()">
        <!-- <i class="icon iconfont icon-refresh" aria-hidden="true" /> -->
        <i class=" icon el-icon-refresh-right" aria-hidden="true" />
        刷新
      </el-button>
      <el-button v-for="(item,i) in columns" :key="i" type="button" class="btn-primary" @click="customClick(item.type)">
        <!-- <i :class="['icon iconfont' ,item.icon ? item.icon :'']" aria-hidden="true" /> -->
        <i :class="['icon',item.icon.indexOf('el-icon')!==-1?item.icon: ['iconfont' ,item.icon ? item.icon :'']]" type="primary" aria-hidden="true" />
        {{ item.label }}
      </el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "TableElement",
  props: {
    title: {
      type: String,
      default: function() {
        return '';
      }
    },
    color: {
      type: String,
      default: function() {
        return '';
      }
    },
    columns: {
      type: Array,
      default: function() {
        return [];
      }
    },
  },
  inject: ["reload"],
  data() {
    return {
      input3: ''
    }
  },
  mounted() { },
  methods: {
    customClick(type) {
      this.$emit('customClick', type)
    },
    click() {
      // this.$emit('click')
    },
  }
}
</script>

<style lang="scss" scoped>
// 测试搜索框
.input-with-select {
  ::v-deep .el-input-group__append {
    padding: 0;
    padding-right: 20px;
    background: #fff;
    border: none;
    // .btn-primary {
    //   color: #000000;
    // }
  }
}

.left-logo {
  // width: 3px;
  // margin-right: 10px;
  // float: left;
  // height: 22px;
  // margin-top: 19px;
  width: 3px;
  margin-right: 10px;
  height: 22px;
}
.btn-primary {
  outline: none;
  color: #000000 !important;
  margin-left: 0;
  border: none;
  cursor: pointer;
  padding: 10px 12px;
  white-space: nowrap;
  span {
    display: flex;
    align-items: center;
  }
  .icon {
    font-weight: 600;
    color: #0177d5;
    font-size: 16px;
    margin-right: 1px;
  }
}
.btn-primary:hover {
  background: #0177d5;
  color: #fff !important;
  text-align: center;
  .icon {
    font-weight: 700;
    color: #fff;
  }
}
</style>

